<template>
  <div style="max-width: 100%;box-sizing: border-box;">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="投苗申请"
      subtitle="记录"
      subtitlePath="/castSeedsRecord">
    </myHeader>
    <div style="padding:.12rem 0;background:#f8f8f8;" class="mt4">
      <mt-cell title="养户：" :value="name"></mt-cell>
      <mt-cell title="饲养规模：" :value="size+' 头'"></mt-cell>
      <mt-field label="申请投苗日期：" placeholder="请选择投苗日期" v-model="castData.applyDate" @click.native="selectApplyDate"
                class="zb-field"></mt-field>
      <mt-field
        label="申请投苗数量："
        placeholder="请输入投苗数量"
        v-model="castData.num"
        @input.native="changeApplyNumber"
        class="zb-field"
      ></mt-field>
    </div>
    <div v-for="(option,index) in options">
      <mt-cell :title="option.title" class="item-checkbox-body">
        <span class="zb-checkbox">
          <span>
            <input
              type="checkbox"
              :value="option.value || option"
              class="checkboxItem"
              :id="'check'+index"
              v-model="option.value"
            >
            <label :for="'check'+index"></label>
          </span>
        </span>
      </mt-cell>
    </div>
    <div class="submit-bth" :class="{active:isActive}" @click="submitPageData()" :btnText="submitBtnText"
    :customStyle="myCustomStyle">提交</div>
    <!--选择日期-->
    <mt-datetime-picker
      ref="deadPicker"
      type="date"
      v-model="selectDate"
      @confirm="applyDateConfirm"></mt-datetime-picker>
  </div>
</template>
<script>
  import myHeader from '@/components/Header'
  import SubmitBtn from '@/components/SubmitBtn'
  import {saveToumiaoApply, queryTouMiaoInitData} from '@/api/business'
  import {getUserInfo} from '@/utils/auth'
  import {Toast} from 'mint-ui'
  import {formatDate, nowDate} from '@/utils/tool'
  import {inputLimitNum} from "@/utils/validator";
  import {mapGetters} from "vuex"
  
  export default {
    name: 'CastSeedsApply',
    components: {
      myHeader,
      SubmitBtn
    },
    data() {
      return {
        castData: {
          applyDate: formatDate(new Date(), 'yyyy-MM-dd'),
          num: '',
        },
        size: '',
        name: '',
        selectDate: new Date(),
        isActive: false,
        options: [
          {
            title: '是否准备加药桶',
            value: false
          },
          {
            title: '上猪台是否准备',
            value: false
          },
          {
            title: '消毒工作是否准备',
            value: false
          },
          {
            title: '加温/降温设施是否达到标准',
            value: false
          },
          {
            title: '圆盘料槽是否准备（50头/个）',
            value: false
          },
          
          {
            title: '发电机是否正常使用（15KW以上）',
            value: false
          },
          {
            title: '保温灯是否准备（每栏1个，护理 栏2个）',
            value: false
          },
          
          {
            title:
              '保温箱、屋中屋是否准备（底板实木板侧板压缩版，每头猪面积不少于0.1平方）',
            value: false
          },
          {
            title:
              '物资准备（每100头猪：米糠100斤、生石灰100斤、米25斤、奶粉2斤、生姜10斤、红糖10斤）',
            value: false
          }
        ],
      }
    },
    created() {
      this.selectDate = nowDate().slice(0, 10);
      this._getInitData();
      this.name = this.userInfo.name
    },
    watch: {
      options: {
        handler(newOptions, oldOptions) {
          this.isActive = newOptions.every((currentValue) => {
            return currentValue.value === true;
          });
        },
        immediate: true,
        deep: true
      }
    },
    computed: {
      ...mapGetters(['userInfo']),
    },
    mounted() {
    
    },
    methods: {
      changeApplyNumber(e) {
        this.castData.num = inputLimitNum(e);
      },
      submitPageData() {
        const requestData = {
          barrelReadied: this.options[0].value,
          pigTableReadied: this.options[1].value,
          disinfectFinished: this.options[2].value,
          temperatureControlReadied: this.options[3].value,
          troughReadied: this.options[4].value,
          electricGeneratorReadied: this.options[5].value,
          heatLampReadied: this.options[6].value,
          incubatorReadied: this.options[7].value,
          materialsReadied: this.options[8].value,
        };
        const totalData = {castName: this.name, scope: this.size, ...this.castData, ...requestData};
        if(!this.castData.num){
          this.$toast('请输入投苗数量！');
          return false;
        }

        if (this.isActive) {
          if (!this.isDisabledSubmit) {
            this.isDisabledSubmit = true;
            this.submitBtnText = '提交中...';
            saveToumiaoApply(totalData)
              .then(res => {
                Toast({
                  message: '提交成功！',
                  duration: 1000
                });
                this.options.forEach((element) => {
                  element.value = false;
                });

                this.$router.push('/castSeedsRecord');
              })
              .catch(err => {
                this.isDisabledSubmit = false;
                this.submitBtnText = '提交';
                Toast({
                  message: err,
                  duration: 1000
                })
              })

          }

        } else {
          Toast({
            message: '请确认投苗申请条件！',
            duration: 1000
          })
          return false;
        }
      },
      selectApplyDate() {
        this.$refs.deadPicker.open();
      },
      applyDateConfirm(date) {
        this.castData.applyDate = formatDate(this.selectDate, 'yyyy-MM-dd');
      },
      _getInitData() {
        this.size = JSON.parse(sessionStorage.getItem('userInfo')).size;
      }
    }
  }
</script>
<style scoped lang="scss">
  .item-checkbox-body {
    padding: 0.04rem 0;
    min-height: 37px !important;
  }
  
  .zb-checkbox {
    position: absolute;
    right: 0.3rem;
    
    span {
      position: relative;
      bottom: 0.1rem;
    }
    
    .checkboxItem {
      position: absolute;
      visibility: hidden;
      
      + label {
        position: absolute;
        width: 0.22rem;
        height: 0.22rem;
        border: 1px solid #cccccc;
        border-radius: 50%;
        background-color: #ffffff;
      }
      
      &:checked {
        + label {
          background-color: #f89b31;
          border: 1px solid #f89b31;
        }
        
        + label:after {
          content: "";
          position: absolute;
          left: 0.04rem;
          top: 0.05rem;
          width: 0.12rem;
          height: 0.06rem;
          border: 2px solid #ffffff;
          border-top-color: transparent;
          border-right-color: transparent;
          transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
          -moz-transform: rotate(-45deg);
          -webkit-transform: rotate(-45deg);
        }
      }
    }
  }
  
  .submit-bth {
    font-size: .16rem;
    margin: .2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    border-radius: .3rem;
    height: .4rem;
    background: linear-gradient(90deg, rgba(128, 123, 120, 1) 0%, rgba(88, 78, 73, 1) 100%);
    color: #ffffff;
  }
  
  .active {
    background: linear-gradient(90deg, rgba(248, 155, 49, 1) 0%, rgba(255, 106, 29, 1) 100%);
    color: #fff
  }
</style>



